.footer__wrapper {
  width: 100%;
  min-height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: 1fr;
}

@media (min-width: 1024px) {
  .footer__wrapper {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
  }
}
